* {
  box-sizing: border-box;
  overflow-x: hidden;
  margin: 0;
  padding: 0;
  font-weight: bold;
}

.heading {
  color: white;
  letter-spacing: 1px;
  font-size: 2rem;
  padding: 1rem;
  background-color: #744b9b;
  text-align: center;
  word-spacing: 6px;
}

p {
  color: white;
  font-size: 1rem;
  text-align: center;
}

input {
  border: none;
  border-bottom: 2px solid #744b9b;
  padding: .2rem;
  margin-bottom: 1rem;
  outline: none;
}

.main {
  display: flex;
  background-color: rgb(108, 141, 197);
  justify-content: center;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
}

.center-div {
  background-color: white;
  padding: 2rem 0;
  text-align: center;
  width: 20rem;
  height: 30rem;
  border-radius: .6rem;
}

button {
  background-color: #744b9b;
  color: white;
  border-radius: 50%;
  font-weight: bolder;
  border: none;
  font-size: 26px;
  width: 3rem;
  height: 3rem;
  margin-left: 1rem;
}

button:hover {
  background-color: rgb(53, 161, 100);
}

ol {
  padding: 0;
  width: 23rem;
  display: flex;
  flex-direction: column;
  gap: .5rem;
}

.todo {
  position: relative;
  display: flex;
  height: 3.4rem;
}

li {
  text-transform: capitalize;
  padding: .7rem;
  font-size: 1.1rem;
  width: 100%;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  text-align: left;
  padding-left: 3.5rem;
  background-color: rgb(217, 234, 239);
}

span {
  position: absolute;
  top: 16px;
  left: 10px;
  background-color: #744b9b;
  border-radius: 50%;
  font-weight: bold;
  margin-left: .6rem;
  transform: scale(1.6);
  color: white;
}

.fa-times {
  width: 1.3rem;
  height: 1rem;
}

span:hover {
  background-color: rgb(232, 17, 17);
  cursor: pointer;
}